import { useIntl } from '@calmdownval/intl-react';
import Alert from '@mui/material/Alert';
import Box from '@mui/material/Box';
import React from 'react';
import { useLocation, useRouteError } from 'react-router-dom';
//
import { AppShell } from '~/components/AppShell';
import { LinkButton } from '~/components/LinkButton';
import * as Route from '~/routes';

const ERROR_MAPPING: Record<number, string | undefined> = {
	404: 'errors.not-found'
};

export const ErrorPage = () => {
	const { t } = useIntl();
	const location = useLocation();
	const error = useRouteError() as { status?: number } | undefined;
	const phrase = ERROR_MAPPING[error?.status ?? 500] ?? 'errors.unknown';

	return (
		<AppShell>
			<Alert severity='error'>
				{t(phrase, { path: location.pathname })}
			</Alert>
			<Box textAlign='center' sx={{ mt: 3 }}>
				<LinkButton to={Route.Home} variant='contained'>
					{t('main.back-home')}
				</LinkButton>
			</Box>
		</AppShell>
	);
};
