import { useIntl } from '@calmdownval/intl-react';
import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';
import Tabs from '@mui/material/Tabs';
import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
//
import * as Route from '~/routes';

const items = [
	{
		route: Route.Home,
		intlKey: 'menu.home'
	},
	{
		route: Route.MintShop,
		intlKey: 'menu.mint-shop'
	},
	{
		route: Route.Configurator,
		intlKey: 'menu.configurator'
	},
	{
		route: Route.MyCollection,
		intlKey: 'menu.collection'
	}
];

export const NavMenu = () => {
	const { t } = useIntl();
	const location = useLocation();
	const navigate = useNavigate();
	return (
		<Box display='flex' justifyContent='center' width='100%'>
			<Tabs
				value={location.pathname}
				onChange={(_e, value) => navigate(value)}
				variant='scrollable'
				centered
			>
				{items.map(item => (
					<Tab
						key={item.route}
						value={item.route}
						label={t(item.intlKey)}
						disableRipple
					/>
				))}
			</Tabs>
		</Box>
	);
};
